body {
    font-family: 'Roboto', sans-serif;
}

.nav-tabs {
    border: none;

    .nav-item,
    .nav-link {
        border-radius: 0;
        border-bottom: 2px solid transparent;
        color: rgba(#000000, 0.5);
    }

    .nav-item.show .nav-link,
    .nav-link.active {
        background-color: rgba(#000000, 0.07);
        border-bottom-color: rgba(#000000, 0.5);
    }
}

.card {
    &-pricing &-img-top {
        min-width: 150px;
        max-width: 150px;
        min-height: 200px;
        max-height: 200px;

        margin-top: -10px;
        margin-left: -10px;
        margin-bottom: 10px;
    }

    &-book &-img-top {
        min-width: 350px;
        max-width: 350px;
        min-height: 450px;
        max-height: 450px;

        margin-top: -10px;
        margin-left: -10px;
        margin-bottom: 10px;
    }

    &-img-top {
        overflow: hidden;
        border-radius: 5px;
        position: relative;
        z-index: 1010;

        img {
            object-fit: cover;
            width: 100%;
            height: 100%;
        }
    }

    @media (max-width: 575.5px) {
        &-pricing &-img-top {
            min-width: unset;
            max-width: unset;
            width: unset;
            min-height: unset;
            max-height: unset;
            
            margin-top: -15px;
            margin-left: 15px;
            margin-right: 15px;
            margin-bottom: 0;
        }
    }

    @media (max-width: 767.5px) {
        &-book &-img-top {
            min-width: unset;
            max-width: unset;
            width: unset;
            min-height: unset;
            max-height: unset;
            
            margin-top: -15px;
            margin-left: 15px;
            margin-right: 15px;
            margin-bottom: 0;
        }
    }
}

.btn {
    border-radius: 0;
}